import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import Swiper from 'swiper';
import { ScrollToPlugin } from 'gsap/ScrollToPlugin';
import { getWindowSize, onWindowResize } from '../utils';
import plansSwitcher from './plans-switcher';
import pricingTabs from './pricing-tabs';

import 'swiper/css';
import 'ScssComponents/plans-switcher.scss';
import 'ScssComponents/card-product.scss';
import 'ScssComponents/pricing_features.scss';
import 'ScssComponents/plans_cards_mob.scss';

export class PricingBlock {
	get SELECTORS() {
		return {
			tableTrigger: '.js-table-trigger',
			accordion: '.js-fixed-table-wrap',
			accordionContent: '.js-fixed-table-content',
			accordionTrigger: '.js-fixed-table-target',
			featuresContent: '.js-features-content',
			plansCardsMob: '.js-plans-card-mob',
			cardTop: '.js-card-top',
			navLink: '.js-pricing-tabs-nav-item',
			tabs: '.js-pricing-tabs-tab-content',
			navSlider: '.pricing_tabs__nav_list',
			wrapper: '.pricing_tabs',
			cardHeadOverlay: '.js-card-overlay',
		};
	}

	get CLASSNAMES() {
		return {
			accordionActiveState: 'features__list_content--active_state',
			cardProductTopScrollMod: 'card_product__top--scroll_mod',
			tabActiveState: 'pricing_tabs__tab_content--active_state',
			navActiveState: 'pricing_tabs__nav_item--active_state',
			accordionFixedState: 'features__list_content--fixed_state',
		};
	}

	tabsInit() {
		this.navLinks.forEach((link) => {
			link.addEventListener('click', this.handleNavClick);
		});
	}

	handleNavClick(e) {
		e?.preventDefault();

		const id = e?.target?.getAttribute('aria-controls');

		if (!id) return;

		this.toggleTab(id);
	}

	toggleTab(id) {
		this.activeTab?.classList.remove(this.CLASSNAMES.tabActiveState);
		this.activeNav?.classList.remove(this.CLASSNAMES.navActiveState);

		this.activeTab = this.tabs.find(
			({ tab }) => tab.getAttribute('id') === id,
		)?.tab;
		this.activeNav = this.navLinks.find(
			(link) => link.getAttribute('aria-controls') === id,
		);
		this.activeTab?.classList.add(this.CLASSNAMES.tabActiveState);
		this.activeNav?.classList.add(this.CLASSNAMES.navActiveState);

		this.currentTab = id.replace('tab-', '');
		if (this.navSlider?.slideTo) this.navSlider.slideTo(this.currentTab);
		ScrollTrigger.refresh();
	}

	navSliderInit() {
		this.navSliderNode = this.section.querySelector(this.SELECTORS.navSlider);

		if (!this.navSliderNode) return;

		this.navSlider = new Swiper(this.navSliderNode, {
			speed: 800,
			centeredSlides: true,
			slidesPerView: 'auto',

			on: {
				slideChange: (swiper) => {
					const { activeIndex } = swiper;
					this.toggleTab('tab-'.concat(activeIndex));
				},
			},
		});
	}

	navSliderDestroy() {
		if (this.navSlider?.destroy) {
			this.navSlider.destroy();
			this.navSlider = null;
		}
	}

	handleScrollToAccorion(acc) {
		if (this.offsetTop || this.offsetTop === 0) {
			gsap.to(window, {
				scrollTo: {
					y: acc,
					offsetY: this.offsetTop,
					duration: 0.2,
				},
			});
		}
	}

	toggleAccordion(index) {
		const { accordions } = this.tabs[this.currentTab];

		if (!accordions[index]) return;

		if (
			accordions[index].accordion.classList.contains(
				this.CLASSNAMES.accordionActiveState,
			)
		) {
			accordions[index].accordion.classList.remove(
				this.CLASSNAMES.accordionActiveState,
			);
			gsap.to(accordions[index].content, {
				maxHeight: 0,
				duration: 0.2,
			});
			if (accordions[index].fixed) {
				setTimeout(() => {
					accordions[index].accordion.classList.remove(
						this.CLASSNAMES.accordionFixedState,
					);
				}, 500);
			}
			this.handleScrollToAccorion(accordions[index].accordion);
		} else {
			accordions[index].accordion.classList.add(
				this.CLASSNAMES.accordionActiveState,
			);
			gsap.to(accordions[index].content, {
				maxHeight: accordions[index].content.scrollHeight,
				duration: 0.2,
			});

			if (accordions[index].fixed) {
				accordions[index].accordion.classList.add(
					this.CLASSNAMES.accordionFixedState,
				);
			}
		}

		this.refreshTS();
	}

	refreshTS() {
		setTimeout(() => {
			ScrollTrigger.refresh();
		}, 520);
	}

	handleAccordionClick(e) {
		e?.preventDefault();

		const trigger = this.tabs[this.currentTab].accordions.find(
			(accData) =>
				!e.target.classList.contains('js-table-overlay') &&
				(accData.trigger.contains(e.target) || accData.trigger === e.target),
		);

		if (!trigger) return;

		const index = this.tabs[this.currentTab].accordions.indexOf(trigger);

		this.toggleAccordion(index);
	}

	initAccordionsEvents() {
		this.tabs.forEach(({ accordions }) => {
			accordions.forEach((accData) => {
				accData?.trigger?.addEventListener('click', this.handleAccordionClick);
			});
		});
	}

	removeAccordionsEvents() {
		this.tabs.forEach(({ accordions }) => {
			accordions.forEach((accData) => {
				accData?.trigger?.removeEventListener(
					'click',
					this.handleAccordionClick,
				);
			});
		});
	}

	getElementsMaxHeight(arr) {
		if (!arr) return 0;

		arr.forEach((el) => {
			el.style.removeProperty('height');
		});

		const maxHeight = arr.reduce((el1, el2) => {
			const height1 = el1.clientHeight;
			const height2 = el2.clientHeight;
			return height1 > height2 ? height1 : height2;
		});

		arr.forEach((el) => {
			el.style.setProperty('height', `${maxHeight}px`, 'important');
		});

		return maxHeight || 0;
	}

	initAccordionPin() {
		if (this.windowWidth < 1024 && this.windowWidth / this.windowHeight > 1.5)
			return; // disable on album orientation
		if (this.windowWidth > 1023 && this.windowWidth / this.windowHeight > 2.9)
			return; // disable on album orientation

		this.showAllTabs();

		this.tabs.forEach(
			({ cardHeads, tab, accordions, headOverlay, cardsHeadMob }, tabIndex) => {
				const additionalOffset =
					this.windowWidth > 1023 ? headOverlay?.clientHeight || 0 : 0;

				const cardsMaxHeight =
					this.windowWidth > 1023
						? this.getElementsMaxHeight(cardHeads) + additionalOffset
						: cardsHeadMob?.clientHeight || 0;

				this.offsetTop = cardsMaxHeight + accordions[0].trigger.clientHeight;

				const headTiggerConfig = {
					pin: true,
					start: `top-=${additionalOffset} top`,
					end: `bottom-=${this.offsetTop} top`,
					endTrigger: tab,
					// markers: true,
					pinSpacing: false,
					// onEnter: () => {
					// 	this.header.classList.add('header--locked_state');
					// },
					// onLeave: () => {
					// 	this.header.classList.remove('header--locked_state');
					// },
					// onEnterBack: () => {
					// 	this.header.classList.add('header--locked_state');
					// },
					// onLeaveBack: () => {
					// 	this.header.classList.remove('header--locked_state');
					// },
				};

				if (this.windowWidth > 1023) {
					cardHeads.forEach((head) => {
						const scrollTrigger = ScrollTrigger.create({
							trigger: head,
							...headTiggerConfig,
						});

						this.scrollTriggers.push(scrollTrigger);
					});
				} else {
					const scrollTrigger = ScrollTrigger.create({
						trigger: cardsHeadMob,
						...headTiggerConfig,
					});

					this.scrollTriggers.push(scrollTrigger);
				}

				accordions.forEach(({ trigger, accordion }, index) => {
					const scrollTrigger = ScrollTrigger.create({
						trigger,
						pin: true,
						start: `top-=${cardsMaxHeight} top`,
						end: `bottom-=${this.offsetTop} top`,
						endTrigger: tab,
						pinSpacing: false,
						onEnter: () => {
							this.tabs[tabIndex].accordions[index].fixed = true;
							if (
								accordion.classList.contains(
									this.CLASSNAMES.accordionActiveState,
								)
							) {
								accordion.classList.add(this.CLASSNAMES.accordionFixedState);
							}
						},
						onLeaveBack: () => {
							this.tabs[tabIndex].accordions[index].fixed = false;
							accordion.classList.remove(this.CLASSNAMES.accordionFixedState);
						},
					});

					this.scrollTriggers.push(scrollTrigger);
				});
			},
		);

		this.hideAllTabs();
	}

	removeAllScrollTriggers() {
		this.scrollTriggers.forEach((scrollTrigger) => {
			if (scrollTrigger?.kill) scrollTrigger.kill();
		});
	}

	getAccordions(tab) {
		const accordions = Array.from(
			tab.querySelectorAll(this.SELECTORS.accordion),
		);
		return accordions.map((accordion) => {
			return {
				accordion,
				parentTab: accordion.closest(this.SELECTORS.tabs),
				trigger: accordion.querySelector(this.SELECTORS.accordionTrigger),
				content: accordion.querySelector(this.SELECTORS.accordionContent),
			};
		});
	}

	showAllTabs() {
		this.tabs?.forEach(({ tab }) => {
			tab.classList.add(this.CLASSNAMES.tabActiveState);
		});
	}

	hideAllTabs() {
		this.tabs?.forEach(({ tab, index }) => {
			if (index === this.currentTab) return;
			tab.classList.remove(this.CLASSNAMES.tabActiveState);
		});
	}

	getData() {
		this.navLinks = Array.from(
			this.section.querySelectorAll(this.SELECTORS.navLink),
		);
		this.tabs = Array.from(this.section.querySelectorAll(this.SELECTORS.tabs));

		this.tabs = this.tabs.map((tab) => {
			return {
				tab,
				accordions: this.getAccordions(tab),
				cardsHeadMob: tab.querySelector(this.SELECTORS.plansCardsMob),
				cardHeads: Array.from(tab.querySelectorAll(this.SELECTORS.cardTop)),
				headOverlay: tab.querySelector(this.SELECTORS.cardHeadOverlay),
			};
		});
	}

	globalDestroy() {
		this.navSliderDestroy();
		this.removeAccordionsEvents();
		this.removeAllScrollTriggers();
	}

	globalInit() {
		if (this.windowWidth < 1024) {
			this.navSliderInit();
		}

		this.getData();

		this.tabsInit();
		this.initAccordionsEvents();
		this.initAccordionPin();
	}

	globalOnceInit() {
		// plansSwitcher();

		// set initial active tab
		this.navLinks.forEach((link) => {
			if (link.classList.contains(this.CLASSNAMES.navActiveState)) {
				this.toggleTab(link.getAttribute('aria-controls'));
			}
		});
	}

	constructor(section) {
		if (!section) return;

		gsap.registerPlugin(ScrollTrigger, ScrollToPlugin);

		this.scrollTriggers = [];
		this.currentTab = 0;
		this.activeTab = section.querySelector(
			'.'.concat(this.CLASSNAMES.tabActiveState),
		);
		this.activeNav = section.querySelector(
			'.'.concat(this.CLASSNAMES.navActiveState),
		);

		const { windowWidth, windowHeight } = getWindowSize();

		this.windowWidth = windowWidth;
		this.windowHeight = windowHeight;

		this.section = section;
		this.header = document.querySelector('header');
		this.handleNavClick = this.handleNavClick.bind(this);
		this.handleAccordionClick = this.handleAccordionClick.bind(this);

		this.globalInit();
		this.globalOnceInit();

		onWindowResize(() => {
			const { windowWidth, windowHeight } = getWindowSize();

			if (this.windowWidth !== windowWidth) {
				this.windowWidth = windowWidth;
				this.windowHeight = windowHeight;

				this.globalDestroy();
				this.globalInit();

				this.toggleTab('tab-'.concat(this.currentTab));
			} else {
				this.windowWidth = windowWidth;
				this.windowHeight = windowHeight;
			}
			ScrollTrigger.refresh();
		});
	}
}
