const plansSwitcher = () => {
	const switchPriceBlock = document.querySelectorAll('.js-switch-info');
	const switcher = document.querySelectorAll('.js-switch-input');
	const getTabBtn = document.querySelectorAll('.js-pricing-tabs-nav-item');

	switcher.forEach((item) => {
		item.addEventListener('click', () => {});
	});

	if (!switchPriceBlock.length) return;
	if (!switcher.length) return;

	if (getTabBtn.length) {
		getTabBtn.forEach((item) => {
			item.addEventListener('click', () => {
				switchPriceBlock.forEach((item) => {
					item.classList.remove('card_product--active_mod');
				});
				switcher.forEach((item) => {
					// eslint-disable-next-line
					item.checked = false;
				});
			});
		});
	}

	switcher.forEach((switchItem) => {
		const switchItemAttr = switchItem.dataset.switch;

		switchPriceBlock.forEach((switchBlock) => {
			const switchBlockAttr = switchBlock.dataset.switchBlock;

			if (switchBlockAttr === switchItemAttr) {
				switchItem.addEventListener('change', (e) => {
					if (e.target.checked) {
						switchBlock.classList.add('card_product--active_mod');
					} else {
						switchBlock.classList.remove('card_product--active_mod');
					}
				});
			}
		});
	});
};

export default plansSwitcher;
