import 'ScssComponents/workspace-and-wix-tabs.scss';
import 'ScssComponents/workspace-tabs.scss';
import Swiper from 'swiper';
import 'swiper/css';
import { buildSwiper, removeSwiper } from './buildSwiper';
import { onWindowResize, getWindowSize } from '../utils';
import { BREAKPOINTS } from '../utils/constants';
import tabs from './workspace-section-tabs';

const workspaceAndWixTabs = () => {
	tabs('.workspace_wix_tabs__link', '.workspace_wix_tabs__tab');

	const classNames = {
		slider: '.js-workspace-wrapper',
		wrapper: '.js-workspace-swiper',
		navLinks: '.js-worksection-nav-link',
		tab: '.js-worksection-tab',
		activeTabClass: 'workspace_wix_tab--active_state',
	};

	const $sliderWrappers = document.querySelectorAll(classNames.wrapper);
	const $jsTabs = document.querySelectorAll(classNames.tab);

	if (!$sliderWrappers.length) return;

	$sliderWrappers.forEach(($wrapper) => {
		const $slider = $wrapper.querySelector(classNames.slider);

		if (!$slider) return;

		let $swiper;

		const resizeWindow = () => {
			const { windowWidth } = getWindowSize();

			if (windowWidth >= BREAKPOINTS.mediaPoint1) {
				if ($swiper) {
					removeSwiper($slider);
					$swiper = undefined;
					tabs('.workspace_wix_tabs__link', '.workspace_wix_tabs__tab', true);
				}
			} else {
				if (!$swiper) {
					buildSwiper($slider);

					const tabSwitcher = ({ $jsTabs, swiper, index, className }) => {
						if ($jsTabs.length) {
							$jsTabs.forEach(($tab) => $tab.classList.remove(className));
							$jsTabs[index].classList.add(className);
							swiper.slideTo(index);
							const $tabLinkContent = $jsTabs[index].querySelector(
								'.workspace_wix_tabs__item.workspace_wix_tab--active_state .workspace_wix_tabs__nav_content',
							);
							if ($tabLinkContent)
								$tabLinkContent.style.maxHeight = `${$tabLinkContent.scrollHeight}px`;
						}
					};

					$swiper = new Swiper($slider, {
						speed: 800,
						centeredSlides: true,
						slidesPerView: 'auto',
						slideActiveClass: classNames.activeTabClass,

						on: {
							slideChange: (swiper) => {
								const { activeIndex } = swiper;

								tabSwitcher({
									$jsTabs,
									swiper,
									index: activeIndex,
									className: classNames.activeTabClass,
								});
							},
							click: (swiper) => {
								const { clickedIndex } = swiper;

								tabSwitcher({
									$jsTabs,
									swiper,
									index: clickedIndex,
									className: classNames.activeTabClass,
								});
							},
						},
					});
				}
			}
		};

		onWindowResize(() => {
			resizeWindow();
		});
	});
};

export default workspaceAndWixTabs;
