import { onWindowScroll, exist } from '../utils';
import 'ScssComponents/submenu.scss';
import 'ScssComponents/lang-menu.scss';

const header = () => {
	const SELECTORS = {
		header: '.header',
		menuTrigger: '.js-menu-trigger',
		menuClose: '.js-menu-close',
		menuBody: '.js-menu-body',
		subMenuTrigger: '.js-submenu-trigger',
		langMenuClose: '.js-lang-menu-close',
		langTrigger: '.js-lang-trigger',
		subMenu: '.js-submenu',
		langMenu: '.js-lang-menu',
		linkSelectsTrigger: '.js-menu-select',
		subMenuWrap: '.js-submenu-wrap',
		headerWrap: '.header__in',
		activeLink: '.submenu__item--active_mod',
		activeLinkParent: '.header_menu__item',
	};

	const CLASSNAMES = {
		bodyOpenMenuState: 'body--open_menu_state',
		bodyOpenSubMenuState: 'body--open_submenu_state',
		subMenuState: 'submenu_open_state',
		subMenuSelectActiveState: 'submenu_select--open_state',
		langMenuOpenState: 'lang_menu--open_state',
		headerScrollState: 'header--scroll_state',
		// headerScrollPos: 'header--pos_state',
		headerScrollShadowMod: 'header--shadow_mod',
		subMenuTrigger: 'js-submenu-trigger',
		activeMenuItem: 'header_menu__item--active_mod',
	};

	const $body = document.body;
	const $header = document.querySelector(SELECTORS.header);
	const $headerWrap = document.querySelector(SELECTORS.headerWrap);
	const $langMenu = document.querySelector(SELECTORS.langMenu);
	const $activeLinks = document.querySelectorAll(SELECTORS.activeLink);
	const lockPaddingValue = window.innerWidth - document.body.offsetWidth;

	const $linkSelectsTrigger = document.querySelectorAll(
		SELECTORS.linkSelectsTrigger,
	);

	let isMenuOpen = false;
	let unlock = true;
	const timeout = 300;

	if (!exist($header)) return;

	$activeLinks.forEach(($link) => {
		const $parentItem = $link.closest(SELECTORS.activeLinkParent);
		if (
			$parentItem &&
			!$parentItem.classList.contains(CLASSNAMES.activeMenuItem)
		)
			$parentItem.classList.add(CLASSNAMES.activeMenuItem);
	});

	const toggleMenu = (open) => {
		$body.classList.toggle(CLASSNAMES.bodyOpenMenuState, open);
		isMenuOpen = open;
	};

	$header.addEventListener('click', (e) => {
		const targetElement = e.target;

		if (targetElement.closest(SELECTORS.menuTrigger)) {
			toggleMenu(true);
		}

		if (targetElement.closest(SELECTORS.menuClose)) {
			toggleMenu(false);
		}

		if (targetElement.closest(SELECTORS.langTrigger)) {
			$langMenu.classList.add(CLASSNAMES.langMenuOpenState);
		}

		if (targetElement.closest(SELECTORS.langMenuClose)) {
			$langMenu.classList.remove(CLASSNAMES.langMenuOpenState);
		}
	});

	let prevScrollPos = window.pageYOffset;
	let currentScroll = prevScrollPos;

	const headerScroll = () => {
		currentScroll = window.pageYOffset;

		if (
			prevScrollPos > 80 &&
			!$header.classList.contains(CLASSNAMES.headerScrollShadowMod)
		) {
			$header.classList.add(CLASSNAMES.headerScrollShadowMod);
		} else if (prevScrollPos < 80) {
			$header.classList.remove(CLASSNAMES.headerScrollShadowMod);
		}

		if (currentScroll <= 80 || prevScrollPos > currentScroll) {
			$header.classList.remove(CLASSNAMES.headerScrollState);
		} else {
			$header.classList.add(CLASSNAMES.headerScrollState);
		}

		prevScrollPos = currentScroll;
	};

	onWindowScroll(headerScroll);

	const selectActiveState = ($subMenuWrap, $linkSelect) => {
		$linkSelect.classList.add(CLASSNAMES.subMenuSelectActiveState);
		// eslint-disable-next-line no-param-reassign
		$subMenuWrap.style.maxHeight = `${$subMenuWrap.scrollHeight * 0.1}rem`;
	};

	const remuveSelectActiveState = ($subMenuWrap, $linkSelect) => {
		// eslint-disable-next-line no-param-reassign
		$subMenuWrap.style.maxHeight = null;
		$linkSelect.classList.remove(CLASSNAMES.subMenuSelectActiveState);
	};

	const openSelect = ({ target }) => {
		const $linkSelect = target?.closest(SELECTORS.linkSelectsTrigger);
		const isClickSelectTitle = target?.classList.contains(
			CLASSNAMES.subMenuTrigger,
		);
		if ($linkSelect && isClickSelectTitle) {
			const $subMenuWrap = $linkSelect?.querySelector(SELECTORS.subMenuWrap);
			if ($linkSelect.classList.contains(CLASSNAMES.subMenuSelectActiveState)) {
				remuveSelectActiveState($subMenuWrap, $linkSelect);
			} else {
				selectActiveState($subMenuWrap, $linkSelect);
			}
		}
	};

	const initSelectsMenu = () => {
		$linkSelectsTrigger.forEach(($linkSelect) => {
			$linkSelect.addEventListener('click', openSelect);
		});
	};

	const destroySelectsMenu = () => {
		$linkSelectsTrigger.forEach(($linkSelect) => {
			$linkSelect.removeEventListener('click', openSelect);
		});
	};

	const bodyHeaderLock = () => {
		$headerWrap.style.paddingRight = `${lockPaddingValue}px`;
		$body.style.paddingRight = `${lockPaddingValue}px`;
		$body.classList.add(CLASSNAMES.bodyOpenSubMenuState);

		unlock = false;
		setTimeout(() => {
			unlock = true;
		}, timeout);
	};

	const bodyHeaderUnLock = () => {
		$body.style.paddingRight = '';
		$headerWrap.style.paddingRight = '';
		$body.classList.remove(CLASSNAMES.bodyOpenSubMenuState);
		unlock = false;
		setTimeout(() => {
			unlock = true;
		}, timeout);
	};

	let handleSubMenuClick = false;
	let $activeSubMenuList = null;

	const removeSubMenuActiveState = () => {
		bodyHeaderUnLock();
		if ($activeSubMenuList) {
			$activeSubMenuList.classList.remove(CLASSNAMES.subMenuState);
			$activeSubMenuList = null;
		}
	};

	const handleToggleSubMenu = ({ target }) => {
		if (target.closest(SELECTORS.subMenuTrigger)) {
			const $linkParent = target.parentNode;
			const $subMenu = $linkParent.querySelector(SELECTORS.subMenu);
			bodyHeaderLock();

			$subMenu.classList.add(CLASSNAMES.subMenuState);

			$activeSubMenuList = $subMenu;
		} else if (
			(!$activeSubMenuList || !$activeSubMenuList.contains(target)) &&
			$activeSubMenuList
		) {
			removeSubMenuActiveState();
			$activeSubMenuList = null;
		}
	};

	const initResizeSubMenu = () => {
		if (window.innerWidth >= 1200) {
			document.addEventListener('click', handleToggleSubMenu);

			document.addEventListener('keydown', removeSubMenuActiveState);
			destroySelectsMenu();
		} else {
			document.removeEventListener('click', handleToggleSubMenu);
			document.removeEventListener('keydown', removeSubMenuActiveState);
			initSelectsMenu();
		}
	};

	window.addEventListener('load', initResizeSubMenu);
	window.addEventListener('resize', initResizeSubMenu);
};

export default header;
