import 'ScssComponents/contact-us.scss';
import 'tom-select/dist/css/tom-select.css';
import TomSelect from 'tom-select';
import { exist } from '../utils/index';

const contact = () => {
	const selectInit = document.querySelectorAll('.js-tom-select');

	if (!exist(selectInit)) return;

	selectInit.forEach((item) => {
		new TomSelect(item, {}); // eslint-disable-line
	});
};

export default contact;
