import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
// import plansSwitcher from './plans-switcher';
import 'ScssComponents/pricing-tabs.scss';

gsap.registerPlugin(ScrollTrigger);

const pricingTabs = () => {
	const SELECTORS = {
		linkTab: '.pricing_tabs__nav_item',
		contentTab: '.pricing_tabs__tab_content',
	};

	const DEFAULT_CLASSES = {
		linkTabActiveState: 'pricing_tabs__nav_item--active_state',
		contentTabActiveState: 'pricing_tabs__tab_content--active_state',
	};

	const linkSelector = document.querySelectorAll(SELECTORS.linkTab);
	const blockSelector = document.querySelectorAll(SELECTORS.contentTab);
	const linkActiveState = DEFAULT_CLASSES.linkTabActiveState;
	const blockActiveState = DEFAULT_CLASSES.contentTabActiveState;

	const matchMedia = gsap.matchMedia();

	linkSelector.forEach((item) => {
		const toggleTabs = (e) => {
			const currentTab = e.target.getAttribute('aria-controls');
			const curenntContent = document.querySelector(`
				${SELECTORS.contentTab}[id="${currentTab}"]
			`);

			blockSelector.forEach((blockEl) => {
				blockEl.classList.remove(blockActiveState);
				blockEl.setAttribute('aria-hidden', 'true');
			});
			linkSelector.forEach((linkEl) => {
				linkEl.classList.remove(linkActiveState);
				linkEl.setAttribute('aria-expanded', 'false');
			});

			curenntContent.classList.add(blockActiveState);
			curenntContent.setAttribute('aria-hidden', 'false');
			e.target.classList.add(linkActiveState);
			e.target.setAttribute('aria-expanded', 'true');
		};
		item.addEventListener('click', toggleTabs);
	});
};

export default pricingTabs;
