import { onWindowResize, getWindowSize } from '../utils';
import { BREAKPOINTS } from '../utils/constants';

function tabs(link, block, repeatInit) {
	let { windowWidth } = getWindowSize();
	let $parent;
	let $item;
	let $contentNav;
	let linkSelector = document.querySelectorAll(link);
	let blockSelector = document.querySelectorAll(block);
	const classNames = {
		nav: '.workspace_wix_tabs__nav',
		navList: '.workspace_wix_tabs__list',
		navContent: '.workspace_wix_tabs__nav_content',
		mainNavMod: '.workspace_wix_tabs__nav--main_mod',
		subNavMod: '.workspace_wix_tabs__nav--sub_mod',
		contentLink: '.workspace_wix_tabs__btn',
		active: 'workspace_wix_tab--active_state',
	};

	if (linkSelector.length && blockSelector.length) {
		const undateHeightEl = ($item) => {
			if ($item && $parent.classList.contains(classNames.active)) {
				// eslint-disable-next-line no-param-reassign
				$item.style.maxHeight = `${$item.scrollHeight}px`;
			} else {
				linkSelector.forEach(($el) => {
					const $navBlock = $el.closest(classNames.subNavMod);
					if ($navBlock) {
						const $itemNav = $navBlock.querySelector(`.${classNames.active}`);
						if ($itemNav) {
							const $contentLink = $itemNav.querySelector(
								classNames.navContent,
							);
							const isContentOverflowed =
								$contentLink.offsetHeight < $contentLink.scrollHeight;
							if (isContentOverflowed)
								$contentLink.style.maxHeight = `${$contentLink.scrollHeight}px`;
						}
					}
				});
			}
		};

		const removeAvtiveClasses = ($item) => {
			Array.from(
				$item.closest(classNames.nav).nextElementSibling.children,
			).forEach(($blockEl) => {
				$blockEl.classList.remove(classNames.active);
			});
			$item
				.closest(classNames.navList)
				.querySelectorAll('[data-tab]')
				.forEach(($el) => {
					const contentNav = $el.querySelector(classNames.navContent);
					$el.closest('[data-tab]').classList.remove(classNames.active);
					if (contentNav) contentNav.style.maxHeight = '0px';
				});
		};

		const elementClick = (e, item = e.target) => {
			$parent = item.closest('[data-tab]');
			$contentNav = $parent.querySelector(classNames.navContent);
			$item = item;
			let currentData = $parent.dataset.tab;
			let prefix = '[data-tab="';
			let postfix = '"]';
			let selector = block + prefix + currentData + postfix;

			if (e === 'close') {
				removeAvtiveClasses(item);
				return;
			}

			if (!e.target.closest(classNames.contentLink)) e.preventDefault();

			if (windowWidth >= BREAKPOINTS.mediaPoint1) {
				if ($parent.classList.contains(classNames.active)) return;
			} else if (
				windowWidth < BREAKPOINTS.mediaPoint1 &&
				$parent.classList.contains(classNames.active)
			) {
				removeAvtiveClasses($item);
				return;
			}

			removeAvtiveClasses($item);

			document
				.querySelectorAll(selector)
				.forEach((selectorEl) => selectorEl.classList.add(classNames.active));
			$parent.classList.add(classNames.active);
			undateHeightEl($contentNav);
		};

		const onResize = ($item) => {
			if (windowWidth < BREAKPOINTS.mediaPoint1) {
				linkSelector.forEach(($item) => {
					if ($item.closest(classNames.subNavMod)) {
						$item.removeEventListener('mouseenter', elementClick);
					}
				});
			}
			windowWidth = getWindowSize().windowWidth;
			undateHeightEl($item);
		};

		window.removeEventListener('resize', onResize);
		onWindowResize(() => onResize($contentNav));

		linkSelector.forEach(($item) => {
			if (windowWidth < BREAKPOINTS.mediaPoint1) {
				if ($item.closest(classNames.subNavMod)) elementClick('close', $item);
			} else {
				if ($item.closest(classNames.subNavMod))
					$item.addEventListener('mouseenter', elementClick);
			}
			if (!repeatInit) {
				$item.addEventListener('click', (e) => elementClick(e, $item));
			} else {
				if ($item.closest(classNames.mainNavMod)) {
					$item.addEventListener('click', (e) => elementClick(e, $item));
				}
			}
		});
	}
}

export default tabs;
