import 'plyr/src/sass/plyr.scss';
import Plyr from 'plyr';

const initPlyrVideo = () => {
	const videoPlayer = Array.from(
		document.querySelectorAll('.js-video-block'),
	).map(
		(p) =>
			new Plyr(p, {
				invertTime: false,
				hideControls: false,
			}),
	);

	videoPlayer.map((item) =>
		item.on('play', () => {
			item.toggleControls(true);

			videoPlayer.map((itemSecond) => {
				if (itemSecond !== item) {
					itemSecond.stop();
				}
				return itemSecond;
			});
		}),
	);

	videoPlayer.map((item) =>
		item.on('pause ready', () => {
			item.toggleControls(false);
		}),
	);
};

export default initPlyrVideo;
