import contact from 'components/contact';
import offerBlock from 'components/offer-block';
import about from 'components/about';
import customerOneHero from 'components/customer-one-hero';
import customerOneNumbers from 'components/customer-one-numbers';
import customerOneContent from 'components/customer-one-content';
import customerOneRequests from 'components/customer-one-requests';
import caseStudies from 'components/case-studies';
import featureList from 'components/feature-list';
import getStartedSection from 'components/get-started-section';
import getStarted from 'components/get_started';
import workspaceAndWixTabs from 'components/workspace-and-wix-tabs';
import findAdvice from 'components/find_advice';
import primaryProduct from 'components/primary-products';
import faqSection from 'components/faq-section';
// import pricingBlock from 'components/pricing-block';
import plansSwitcher from 'components/plans-switcher';

import generalTerms from 'components/general-terms';
import createWebsite from 'components/create-website';
import createDesign from 'components/create_design';

import workspaceList from 'components/workspace-list';

import layout from 'layout/layout';
import { pageLoad } from './utils';
import { PricingBlock } from './components/pricing-block-refactor';

export default class App {
	constructor() {
		this.$htmlTag = document.querySelector('html');
		this.pageName =
			this.$htmlTag.dataset.templateName &&
			this.$htmlTag.dataset.templateName.length > 0
				? this.$htmlTag.dataset.templateName
				: null;

		this.init = this.init.bind(this);
		this.init();
	}

	importPage() {
		if (this.pageName && this.pageName !== null) {
			import(`./pages/${this.pageName}`)
				.then(({ default: pageFunc }) => {
					const newPage = pageFunc();
				})

				.catch((error) => {
					console.error(
						'Failed to load page, check data-template-name at root if correct',
					);

					console.dir(error, error.stack);
				});
		}
	}

	init() {
		const initLayout = layout();
		pageLoad(() => {
			document.body.classList.add('body--loaded');

			contact();
			about();
			offerBlock();
			customerOneHero();
			customerOneNumbers();
			customerOneContent();
			customerOneRequests();
			caseStudies();
			featureList();
			getStartedSection();
			primaryProduct();
			workspaceAndWixTabs();
			findAdvice();
			getStarted();
			faqSection();
			// pricingBlock();
			const pricingSections = document.querySelectorAll('.pricing_tabs');

			pricingSections.forEach((section) => {
				const instance = new PricingBlock(section);
			});

			generalTerms();
			createWebsite();
			createDesign();

			workspaceList();
			plansSwitcher();
		});
		setTimeout(() => {
			this.importPage();
		}, 0);
	}
}
