import 'ScssComponents/popup-form.scss';

const popupForm = () => {
	const selectors = {
		btn: '.js-popup-form-trigger',
		popups: '.js-popup-form',
		close: '.js-popup-form-btn-close',
		content: '.js-popup-form-content',
		headerWrap: '.header__wrap',
		btnFormSubmit: '.js-popup-form-submit',
		popupMessage: '.js-popup-message',
		inputFormFile: '.js-input-file',
		inputFormFileName: '.popup_form__file_name',
	};

	const classNames = {
		popupActive: 'popup--active_state',
		popupMessageActive: 'popup-message--active_state',
	};

	const $btn = document.querySelectorAll(selectors.btn);
	const $popups = document.querySelectorAll(selectors.popups);
	if (!$popups.length) return;
	const $close = document.querySelectorAll(selectors.close);
	if ($close.length === 0) return;
	const $headerWrap = document.querySelector(selectors.headerWrap);
	const $body = document.body;
	const lockPaddingValue = window.innerWidth - document.body.offsetWidth;
	const $btnFormSubmit = document.querySelectorAll(selectors.btnFormSubmit);
	if ($btnFormSubmit.length === 0) return;
	const $inputFormFile = document.querySelector(selectors.inputFormFile);
	const $inputFormFileName = document.querySelector(
		selectors.inputFormFileName,
	);

	let unlock = true;
	const timeout = 300;

	const bodyLock = () => {
		$headerWrap.style.paddingRight = `${lockPaddingValue}px`;
		$body.style.paddingRight = `${lockPaddingValue}px`;
		$body.classList.add('body--popup_open');

		unlock = false;
		setTimeout(() => {
			unlock = true;
		}, timeout);
	};

	const bodyUnLock = () => {
		$body.style.paddingRight = '';
		$headerWrap.style.paddingRight = '';

		unlock = false;
		setTimeout(() => {
			unlock = true;
		}, timeout);
	};

	function popupClose(popupActive, doUnlock = true) {
		if (unlock) {
			const popupActive = document.querySelector(`.${classNames.popupActive}`);
			const popupMessageActiveState = document.querySelector(
				`.${classNames.popupMessageActive}`,
			);
			popupActive?.classList.remove(classNames.popupActive);

			if ($body.classList.contains('body--popup_open')) {
				$body.classList.remove('body--popup_open');
			}

			if (popupMessageActiveState) {
				popupMessageActiveState?.classList.remove(
					classNames.popupMessageActive,
				);
			}

			if (doUnlock) {
				bodyUnLock();
			}
		}
	}

	document.addEventListener('keydown', (e) => {
		if (e.key === 'Escape') {
			const popupActive = document.querySelector(`.${classNames.popupActive}`);
			popupClose(popupActive);
		}
	});

	function popupOpen(curentPopup) {
		if (curentPopup && unlock) {
			const popupActive = document.querySelector(`.${classNames.popupActive}`);
			if (popupActive) {
				popupClose(popupActive, false);
			} else {
				bodyLock();
				curentPopup.classList.add(classNames.popupActive);
			}

			curentPopup.addEventListener('click', (e) => {
				if (!e.target.closest(selectors.content)) {
					popupClose(curentPopup);
				}
			});
		}
	}

	$popups.forEach(($popup) => {
		$btn.forEach((item) => {
			item.addEventListener('click', (e) => {
				e.preventDefault();
				const trigger = item.dataset.popupTrigger;

				if (trigger === $popup.dataset.popup) {
					popupOpen($popup);
				}
			});
		});

		if (!$close) return null;

		$close.forEach((item) => {
			item.addEventListener('click', () => {
				if (item.closest(`.${classNames.popupActive}`)) {
					popupClose($popup);
				}
			});
		});

		return null;
	});

	// $btnFormSubmit.forEach((btnSubmit) => {
	// 	btnSubmit.addEventListener('click', (e) => {
	// 		e.preventDefault();
	// 		const parentBtnSubmit = btnSubmit.closest(selectors.popups);

	// 		if (!parentBtnSubmit.classList.contains(classNames.popupMessageActive)) {
	// 			parentBtnSubmit.classList.add(classNames.popupMessageActive);
	// 		}
	// 	});
	// });

	// eslint-disable-next-line func-names
	$('.popup_form__btn').on('wpcf7:submit', function (event) {
		const parentBtnSubmit = $(this).closest('.js-popup-form');

		if (!parentBtnSubmit.hasClass('popup-message--active_state')) {
			parentBtnSubmit.addClass('popup-message--active_state');
		}
	});

	$inputFormFile.addEventListener('change', () => {
		const fileInput = $inputFormFile?.files[0];
		if (fileInput) {
			$inputFormFileName.textContent = fileInput.name;
		}
	});
};

export default popupForm;
